<template lang="pug">
	details.add-new
		summary.add-new__summary –î–æ–±–∞–≤–∏—Ç—å —Å—Ç—Ä–æ–∫—É
		form.add-new__form(
			@submit="checkAndAdd"
		)
			.add-new__row(
				v-for="(title, i) in tableHead"
			)
				input.add-new__input(
					v-model="form[title]"
					:type="typesHead[i]"
					:placeholder="title"
					:class="($v.form.id.$dirty && $v.form[title].$invalid)? '.add-new__input--wrong' : ''"
				)
				

				// ===================================
				// * VALIDATION ERRORS ----------------
				p.error(
					v-if="$v.form.$dirty && !$v.form[title].required"
				) –û–±—è–∑–∞—Ç–µ–ª—å–Ω–æ–µ –ø–æ–ª–µ
				p.error(
					v-if="title==='id' && $v.form.$dirty && !$v.form[title].valid"
				) ID –∑–∞–Ω—è—Ç
				p.error(
					v-if="title==='id' && $v.form.$dirty && !$v.form[title].validNumberOnly"
				) –¢–æ–ª—å–∫–æ —Ü–∏—Ñ—Ä—ã
				p.error(
					v-if="title==='firstName' && $v.form.$dirty && !$v.form[title].valid"
				) –° –∑–∞–≥–ª–∞–≤–Ω–æ–π –±—É–∫–≤—ã
				p.error(
					v-if="title==='lastName' && $v.form.$dirty && !$v.form[title].valid"
				) –° –∑–∞–≥–ª–∞–≤–Ω–æ–π –±—É–∫–≤—ã
				p.error(
					v-if="title==='email' && $v.form.$dirty && !$v.form[title].valid"
				) –ù–µ–∫–æ—Ä—Ä–µ–∫—Ç–Ω—ã–π email
				p.error(
					v-if="title==='phone' && $v.form.$dirty && !$v.form[title].valid"
				) –ù–æ–º–µ—Ä –≤ —Ñ–æ—Ä–º–∞—Ç–µ: (123)123-1234
				// * VALIDATION ERRORS ----------------
				// ===================================


		button(
			type="submit"
			@click="checkAndAdd"
		).add-new__button –û–∫
</template>


// ===== SCRIPTS ==============================
<script>
import { validationMixin } from 'vuelidate'
import { required } from 'vuelidate/lib/validators'

export default {
	name: 'AddNewForm',
	mixins: [validationMixin],
	props: {
		tableHead: Array,
		typesHead: Array,
		tableIds: Array,
	},
	data() {
		return {
			ableAdding: false,
			form: {
				id: '',
				firstName: '',
				lastName: '',
				email: '',
				phone: '',
			},
		}
	},
	methods: {
		checkAndAdd(e) {
			this.$v.form.$touch()
			if(this.$v.form.$invalid) {
				e.preventDefault()
			} else {
				this.$emit('add-new', this.form)
				this.form = {
					id: '',
					firstName: '',
					lastName: '',
					email: '',
					phone: '',
				}
				this.$v.$reset()
			}
		},
	},
	validations: {
		form: {
			id: {
				required,
				validNumberOnly(value) {
					return (/^\d*$/).test(value)
				},
				valid(value) {
					return !this.tableIds.includes(value)
				}
			},
			firstName: {
				required,
				valid(value) {
					return (/^[A-Z]/).test(value)
				}
			},
			lastName: {
				required,
				valid(value) {
					return (/^[A-Z]/).test(value)
				}
			},
			email: {
				required,
				valid(value) {
					return value.includes('@')
				},
			},
			phone: {
				required,
				valid(value) {
					return (/^\(\d{3}\)\d{3}-\d{4}/).test(value)
				},
			},
		}
	},
}
</script>


